
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
XHeader,
InlineLoading
},
beforeRouteLeave(to, from, next) { // 用于做缓存

if (to.name == 'symptoms'||to.name == 'diseaseListSearch') {

localStorage.setItem('lastcode_Y', this.$route.params.code || window.localStorage.lastcode_Y)
localStorage.setItem('lastcode_N', this.$route.params.name || window.localStorage.lastcode_N)
} else {
localStorage.removeItem('lastcode_Y');
localStorage.removeItem('lastcode_N');
}
next();
},
data() {
return {
pageIndex: 1,
pageSize: 15,
loconame: '',
list: [






]
};
},
mounted() {
this.initdata();
this.loconame = this.$route.params.name || window.localStorage.lastcode_N
},
methods: {
initdata() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'diseasepool',
'MSH.2': 'listDisease',
'pageIndex': this.pageIndex,
'pageSize': this.pageSize,
'cbm': this.$route.params.code || window.localStorage.lastcode_Y,
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof data != 'undefined') { //判断是否空对象
if (typeof data.rows != 'undefined' && data.rows.length > 0) {

if (this.pageIndex == 1) {
this.list = data.rows
} else {
this.list = this.list.concat(data.rows);
}
} else {
this.judge()
}
} else {
this.judge()
}
})
},
judge() { //无数据判断
if (this.pageIndex > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.list = [];
}
},
pullingDown() {
this.pageIndex = 1;
this.initdata();
},
pullingUp() {
this.pageIndex++;
this.initdata();
},
cancerDetail(name, code) { //点击跳转
this.$router.push({
name: "symptoms",
params: {
code: code,
name: name,
}
});
},
searchbt() {
this.$router.push({
name: "diseaseListSearch",
});
}
}
};



